import React from 'react';

// 
import { Grid } from 'semantic-ui-react';

// 
import Topics from '../components/Topics';

let Posts = () => {
  return (
    <Grid>
      {/* Row =  16*/}
      <Grid.Row>
        {/* Column =  width */}
        <Grid.Column width={3}>
          <Topics />
        </Grid.Column>
        <Grid.Column width={10}></Grid.Column>
        <Grid.Column width={3}></Grid.Column>
      </Grid.Row>
    </Grid>
  );
};

export default Posts;
